/*===========================================
================ CHUNK: BASE/RTYPOGRAPHY
===========================================*/
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto/Roboto-Regular.ttf");
}
html {
  font-size: min(1rem + 0.1764705882vw, 1.35rem);
  font-family: "Roboto", sans-serif;
}

/*===========================================
================ CHUNK: BASE/RESET
===========================================*/
* {
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  min-height: 100vh;
}

button, input, select, textarea {
  width: 190px;
}

p {
  max-width: 600px;
}

h1 {
  margin-top: 0;
}

a,
a:active,
a:hover,
a:visited {
  text-decoration: none;
  color: #3882F6;
}

.container {
  width: 80%;
  margin: 0 auto;
}

/*===========================================
================ CHUNK: BASE/LAYOUT
===========================================*/
header {
  padding: 20px 0;
  background: #1F2937;
  color: #E9E9E9;
}
header ul {
  display: flex;
  justify-content: right;
  padding: initial;
}
header li {
  list-style: none;
  font-size: 0.9rem;
}
header li:nth-of-type(n+2) {
  margin-left: 20px;
}
header li.logo {
  font-size: 1.4rem;
  margin-right: auto;
}

section.hero {
  background-color: #1F2937;
  color: #E9E9E9;
}
section.hero .container {
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
}
section.hero .container > div {
  flex: 1;
}
section.hero .container .left h1 {
  max-width: 300px;
}
section.hero .container .left button {
  width: auto;
}
section.hero .container .right {
  background-color: rgba(200, 200, 200, 0.3);
  text-align: center;
  align-items: center;
}

section.cards {
  padding: 40px 0;
}
section.cards h2 {
  color: #1F2937;
  text-align: center;
  margin: 0 0 40px 0;
}
section.cards .flex-container {
  display: flex;
  justify-content: space-evenly;
}
section.cards .flex-container > div {
  width: 130px;
}
section.cards .flex-container > div .card {
  border: 2px solid #3882F6;
  border-radius: 8px;
  height: 130px;
}
section.cards .flex-container > div p {
  margin-top: 10px;
  color: #888;
  text-align: center;
}

section.quote {
  background-color: #E9E9E9;
  color: #555;
  padding: 40px 0;
}
section.quote .container {
  max-width: 600px;
}
section.quote p {
  font-style: italic;
  color: #777;
}
section.quote p.author {
  max-width: initial;
  text-align: right;
  font-weight: bold;
  color: #555;
}

.cta-container {
  padding: 40px 0;
}
.cta-container section.call-to-action {
  border-radius: 10px;
  padding: 40px 90px;
  background-color: #3882F6;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cta-container section.call-to-action .content {
  color: #E9E9E9;
}
.cta-container section.call-to-action .content h2 {
  margin: 0;
}
.cta-container section.call-to-action .content p {
  margin: 10px 0 0 0;
}
.cta-container section.call-to-action button {
  border: 1px solid #E9E9E9;
  width: 100px;
}

footer {
  background-color: #1F2937;
  padding: 20px 0;
  color: #E9E9E9;
  text-align: center;
}
footer p {
  margin: 0;
  max-width: initial;
}

button {
  background: #3882F6;
  border: none;
  font-size: 0.7rem;
  border-radius: 5px;
  padding: 5px 15px;
  color: #E9E9E9;
}